<template>
    <v-touch @swipeleft="$emit('swipeleft')" @swiperight="$emit('swiperight')"
        v-bind:swipe-options="{ direction:'horizontal' }" :id="id" class="mescroll">
        <!-- , MeScroll  mescroll-upwarp  -->
        <div>
            <slot></slot>
        </div>
        <div v-show="!loading&&(!value||!value.length)">
            <slot name="empty"></slot>
        </div>
    </v-touch>
</template>
<script>
    import MeScroll from './mescroll.m'
    import './mescroll.min.css'
    export default {
        name: "ui-pull",
        props: {
            value: Array, //list
            total: Number,
            disablePullUp: {
                type: Boolean,
                default: false
            },
            disablePullDown: {
                type: Boolean,
                default: false
            },
            num: Number,
            pageSize: {
                type: Number,
                default: 10
            },
            immediateCheck:{//
                type:Boolean,
                default: true
            },
            emptyText:{
                type: String,
                default: '~'
            },
            emptyIcon:{
                type: String,
                default: null
            }
        },
        data() {
            return {
                uiPullInstance: null,
                id: 'ui-pull-' + this._uid,
                initNum: this.num,
                loading: true
            }
        },
        mounted() {
            const CONFIG = {
                down: {
                    use: !this.disablePullDown,
                    callback: this.pullDownCallback, //
                    auto: false, //
                    autoShowLoading: true, //auto=true()
                },
                up: {
                    use: !this.disablePullUp,
                    callback: this.pullUpCallback, //
                    auto: this.immediateCheck, //
                    scrollbar: {
                        use: false
                    }, //
                    empty: !this.$scopedSlots.empty && {
                        warpId: this.id,
                        icon: this.emptyIcon,
                        tip: this.emptyText,
                    },
                    page: {
                        size: this.pageSize
                    },
                }

            }

            this.uiPullInstance = new MeScroll(this.id, CONFIG);
        },
        methods: {
            pullDownCallback(instance) {
                this.$emit('update:num', this.initNum)
                this.$emit('load', instance)
                this.loading = true
            },
            pullUpCallback(dataSize, instance) {
                this.loading = true
                this.$emit('load', instance)
            },
            endSuccess() {
                this.$nextTick(() => {
                    /**
                     * @param {Number} 
                     * @param {Boolean} 
                     */
                    const hasNext = this.value.length < this.total
                    // console.log(':', hasNext);
                    this.uiPullInstance.endSuccess(this.value.length, hasNext)
                    this.loading = false
                })
            },
            // endErr() {
            //     this.uiPullInstance.endErr() //
            // },
            reload() { //tab
                this.$emit('update:num', this.initNum)
                this.$emit('input', [])
                this.uiPullInstance.resetUpScroll() // ()
                this.loading = false
            },
        },
    }
</script>