<style lang="scss" scoped>
    .ui-datetime{
        width: 100vw;
    }
</style>
<template>
    <van-field
        v-model="value"
        :label="label"
        :placeholder="placeholder"
        :required="required"
        :disabled="disabled"
        readonly
        @focus="open()">

        <!-- body -->
        <template v-slot:button>
            <div ref="popup">
                <van-popup v-model="popupVisible" position="bottom" class="ui-datetime" get-container="body">
                    <van-datetime-picker 
                    ref="picker"
                    v-model="pickerModel" 
                    :type="type"
                    :min-date="minDate"
                    :max-date="maxDate"
                    :min-hour="minHour"
                    :max-hour="maxHour"
                    :min-Minute="minMinute"
                    :max-Minute="maxMinute"
                    @confirm="confirm"
                    @cancel="cancel"/>
                </van-popup>
            </div>
        </template>

    </van-field>
</template>
<script>
    export default {
        name:'ui-datetime',
        props:{
            label:String,//
            type:{
                type:String,
                default:'datetime'
            },
            value:{
                type:String,
            },
            required:Boolean,
            disabled:{
                type:Boolean,
                default:false
            },
            placeholder:{
                type:String,
                default:''
            },
            minDate:Date,
            maxDate:Date,
            minHour:Number,
            maxHour:Number,
            minMinute:Number,
            maxMinute:Number,
        },
        data(){
            return {
                popupVisible:false,
                pickerModel:'',
            }
        },
        methods:{
            open(){
                //picker
                if (this.value) {
                    if (this.type==='time') {
                        // 
                        let hour = this.value.split(':')[0]
                        let minute = this.value.split(':')[1]
                        this.$refs.picker.$children[0].setValues([hour,minute])
                    }else {
                        // 
                        this.pickerModel = new Date(this.value);
                    }
                }
                this.popupVisible = true
            },
            close(){
                this.popupVisible = false
            },
            confirm(val){
                // 
                let formate ='yyyy-MM-dd hh:mm:ss'
                switch (this.type) {
                    case 'date':
                        formate='yyyy-MM-dd'
                        break;
                    case 'time':
                        formate='hh:mm:ss'
                        break;
                    case 'year-month':
                        formate='yyyy-MM'
                        break;
                
                    default:
                        break;
                }

                // -
                let timeStr = ''
                if (this.type==='time') {
                    timeStr = val
                }
                else  {
                    timeStr = this.$date(val, formate)
                }

                // 
                this.$emit('input', timeStr)

                this.close()
                this.$emit('confirm',timeStr)
            },
            cancel(){
                this.close()
                this.$emit('cancel')
            }
        },
    }
</script>