<style lang="scss" scoped>
    .ui-picker{
        width: 100vw;
    }
</style>
<template>
    <van-field :label="label" v-model="selectedText" :placeholder="placeholder" readonly :required="required" @focus="open()">
        <!-- body -->
        <template v-slot:button>
            <div ref="popup">
                <van-popup class="ui-picker" v-model="popupVisible" position="bottom" get-container="body">
                    <van-picker
                    show-toolbar
                    :columns="options"
                    @cancel="close"
                    @confirm="confirm"
                    ref="picker"
                    :loading="options.length===0"
                    />
                </van-popup>
            </div>
        </template>
    </van-field>
</template>
<script>
    export default {
        name:'ui-picker',
        props:{
            options:Array,
            value:'',
            label:String,
            required:Boolean,
            placeholder:{
                type:String,
                default:''
            },
            disabled:{
                type:Boolean,
                default:false
            },
            valueKey:{
                type:String,
                default:'text'
            }
        },
        data(){
            return {
                popupVisible:false,
                reRenderSwitch:true,//picker
                selectedText:'',
                selectedIndex:0
            }
        },
        mounted(){
            this.syncByModel()
        },
        watch:{
            value(v){
                this.syncByModel()
            },
            popupVisible(v){
                if (v===true) {
                    this.$nextTick(() => {
                        this.$refs.picker.setIndexes([this.selectedIndex])
                    })
                }
            }
        },
        methods:{
            confirm(item){
                // v-model
                this.$emit('input',item.id)
                // @change
                if (this.selectedText) {
                    this.$emit('change', item)
                }
                // 
                this.close()
            },
            open(){
                if (!this.disabled){
                    this.popupVisible=true
                }
            },
            close(){
                this.$nextTick(() => {
                    this.popupVisible=false
                })
            },
            syncByModel(){
                if (!this.value) return;
                    
                this.options.forEach((item,i)=>{
                    if (item.id === this.value) {
                        this.selectedText = item[this.valueKey]
                        this.selectedIndex=i
                    }
                })
            }
        },
    }
</script>
